#nav > *{
  padding-left: 10px;
}


body{
  background-color: rgb(153, 112, 161);
  font-family: Poppins, "Sans Serif";
}
#content{
  padding: 15px;
}
.cardy{
  margin-top: 20px;
  border-radius: 13px;
  width: 30vw;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.97) 40%, rgba(0, 0, 0, 0.75));
  color: white;
}

.addRow{
  display: flex;
  justify-content: center;
  align-items: center;
}

#listing{
  margin: 10px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 8px;

}
#btn {
  margin-right: 10px;
}

#listing > input[type = 'text'] {
  background-color: transparent;
  border: none;
  color: white;
  text-transform: uppercase;
}

